<template>
  <div>
    <el-dialog
        title=""
        :visible.sync="dialogVisible"
        :close-on-click-modal="false"
        width="471px"
        :before-close="handleClose"
    >
        <div class="dialog-center" style="text-align: center;">
            
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button size="medium" type="" class="w-90 plain-btn-style" @click="handleClose" round></el-button>
            <el-button size="medium" type="primary" class="w-90"  @click="changeStatus" round></el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
//
function doLogout() {
  var params = {};
  params.logoutMode = 1;
  params.removeBinding = 0;
  CTILink.Agent.logout(params, function(result) {
    if (result.code == 0) {
      //
    }
  });
}

//
function doPause() {
  var params = {};
  params.pauseType = 1;
  params.pauseDescription = "";
  CTILink.Agent.pause(params);
}

//
function doUnpause() {
  CTILink.Agent.unpause();
}

//
function doPreviewOutcall(tel) {
  var params = {};
  params.tel = tel;
  CTILink.Agent.previewOutcall(params);
}

/**  */
//
function doUnlink() {
  if (CTILink.Session.isSessionAlive()) {
    CTILink.Session.unlink();
  }
}

//
function doConsult(tel) {
  if (CTILink.Session.isSessionAlive()) {
    var params = {};
    params.objectType = 0;
    params.consultObject = tel;
    CTILink.Session.consult(params);
  }
}

/**  */
//
function handleState(event) {
  console.log(event);

  // : 
  var stateShow = event.state;
  switch (event.state) {
    case "":
      break;
    case "":
      //, 
      doLogout();
      break;
    case "":
      break;
    case "":
      break;
    case "":
      break;
    case "":
      switch (event.stateAction) {
        case "ringingIb": //
          break;
        case "ringingAgentOb": //
          break;
        case "ringingCustomerOb": //
          break;
      }
      break;
    case "":
      switch (event.stateAction) {
        case "busyIb": //
          break;
        case "busyOb": // 
          break;
        case "hold": //
          stateShow = "";
          break;
        case "unhold": //
          break;
        case "consultStart": //
          break;
        case "consultLink": //
          stateShow = "";
          break;
        case "busyConsult": //
        case "busyTransfer":
          break;
        case "consultThreeway":
          stateShow = "";
        default:
      }
      break;
    case "": //
      break;
  }
  console.log(event.state, event.stateAction);
  // $('#status').html(event.state);
  // $('#stateAction').html(event.stateAction);
  window.eventBus.$emit("stateAction", event.stateAction);
  window.localStorage.setItem('stateAction', event.stateAction)
  if(event.stateAction == 'ringingIb') {
    window.eventBus.$emit("customerNumber", event.customerNumber);
  }
}

//
function handleRinging(event) {
  console.log("");
}

//
function handleKickout(event) {
  if (event.code == 0) {
    //
    console.log(",");
  }
}

//
function refuse() {
  var params = {};
  params.description = "";
  CTILink.Session.refuse(params);
}
export default {
  data() {
    return {
      dialogVisible: false,
    };
  },
  created() {
    let that = this;
    that.load_script(
      "//agent-gateway-1.cticloud.cn/js/agent/v10/cti-link.js",
      function() {
        that.load_script(
          "//lib.sinaapp.com/js/jquery/3.1.0/jquery-3.1.0.min.js",
          function() {
            that.load_script(
              "//cdn.bootcss.com/blueimp-md5/2.13.0/js/md5.min.js",
              function() {
                that.initSDK();
              }
            );
          }
        );
      }
    );
  },
  methods: {
    load_script(url, callback) {
      var script = document.createElement("script");
      script.type = "text/javascript";
      if (script.readyState) {
        //IE
        script.onreadystatechange = function() {
          if (
            script.readyState == "loaded" ||
            script.readyState == "complete"
          ) {
            script.onreadystatechange = null;
            callback();
          }
        };
      } else {
        //others
        script.onload = function() {
          callback();
        };
      }
      script.src = url;
      document.getElementsByTagName("head")[0].appendChild(script); // document.body.appendChild(script);
    },
    initSDK() {
      let that = this;
      // $(document).ready(function () {
      CTILink.setup({ webrtc: true, debug: true }, function() {
        //CTILink

        //
        /**  */
        CTILink.event("status", handleState); //
        CTILink.event("previewOutcallRinging", handleState); //
        CTILink.event("previewOutcallBridge", handleState); //
        CTILink.event("consultStart", handleState); //
        CTILink.event("consultLink", handleState); //
        CTILink.event("consultError", handleState); //
        CTILink.event("unconsult", handleState); ////
        CTILink.event("consultThreeway", handleState); //
        CTILink.event("consultThreewayUnlink", handleState); //
        CTILink.event("consultTransfer", handleState); //

        /**  */
        CTILink.event("ringing", handleRinging); //
        CTILink.event("kickout", handleKickout); //
        if(window.localStorage.getItem('c_LoginStatus') && window.localStorage.getItem('c_LoginStatus') == 'true'){
          that.dialogVisible = true;
        }else{
          //
          let c_use_tel = window.localStorage.getItem('c_use_tel');
          if(c_use_tel == 3){
              that.doLogin();
          }else if(c_use_tel == 1){
              that.doLogin('17322300397', 1);
          }
        }

        
      });
      // });
    },
    //
    doLogin(bindTel='100002', bindType=3) {
      //sessionKey, qidswebSocketUrl

      var departmentId = "BM3000527";
      var crmId = "0001";
      var timestamp = parseInt(new Date().getTime() / 1000);
      var token = "acfd80b3a36bec1acd79da0710684687";
      var sign = md5(departmentId + timestamp + token);

      var url =
        "https://api-test-2.cticloud.cn/interface/v10/agentLogin/authenticateJsonp";
      url +=
        "?validateType=1&departmentId=" +
        departmentId +
        "&crmId=" +
        crmId +
        "&timestamp=" +
        timestamp +
        "&sign=" +
        sign;
      $.ajax({
        type: "GET",
        dataType: "jsonp",
        jsonp: "callback",
        url: url,
        success: function(r) {
          var data = eval("(" + r + ")");
          if (data != undefined && data.result == "0") {
            var params = {};
            params.sessionKey = data.sessionKey;
            params.enterpriseId = data.enterpriseId; //
            params.cno = data.cno;
            params.bindTel = bindTel; //'100002'  bindType 
            params.bindType = bindType; //123
            params.loginStatus = 1;
            params.webSocketUrl = data.agentGateWayUrl;
            params.qids = data.qids; //

            CTILink.Agent.login(params, function(result) {
              if (result.code == 0) {
                //
                console.log("");
                console.log(result);
                
              } else {
                // + result.msg
                console.log(result);
              }
            });
          }
        },
        error: function(r) {
          //
        }
      });
    },
    handleClose(){
      this.dialogVisible = false;
      window.localStorage.removeItem('c_LoginStatus');
      window.eventBus.$emit("stateAction", 'offline');
      window.localStorage.setItem('stateAction', 'offline')
    },
    changeStatus(){
      // 
      this.doLogin();
      window.sessionStorage.setItem('c_RLtransferStatus','1');
      this.dialogVisible = false;
      window.localStorage.removeItem('c_LoginStatus');
      
    }
  }
};
</script>

<style lang="scss" scoped>
</style>






